<script>
import Scroller from './conponents/Scroller';
export default {
  name: "TestContent",
  components: {
    Scroller
  },
  mounted() {
    this.myRandom()
  },
  methods: {
    // 伪随机数
    myRandom() {
      var rand = (function(){
        var today = new Date(); 
        var seed = today.getTime();
        function rnd(){
          seed = ( seed * 9301 + 49297 ) % 233280;
          return seed / ( 233280.0 );
        };
        return function rand(number){
          // return Math.ceil(rnd(seed) * number);
          return Math.ceil(rnd() * number);
        };
      })();
      var myNum = [rand(5),rand(5),rand(5),rand(5),rand(5),rand(5)];
      console.log(myNum);
    }
  },
}
</script>
